import React from 'react';
import PropTypes from 'prop-types';
import BillingAddress from '../container/BillingAddress';
import ManualForm from '../container/ManualForm';
import Icon from './Icon';
import config from '../config/config';
import isError from '../actions/isError';
import paypalService from '../actions/paypal';
import shippingService from '../actions/useShippingAddress';

class DifferentCard extends React.Component {

  constructor(props){
    super(props);
  }

  componentWillMount() {
    const selectedState = "differentCard";

    if (shippingService.useAddress() && !this.props.shippingAddressLoaded) {
      this.props.onUpdateShippingAddress(this.props.addressValues, this.props.errorArray);
    }

    if (this.props.cards.length === 0  && !paypalService.getPayPalStatus()) {
      this.props.onCardSelect(selectedState)
    }
  }

  render(){
    const {selectedMethod, addressValues, addressLookupData, addressLookupCalled, manualForm, multiFormChange, onCardSelect, onFormChange, onShowManualForm, onAddressLookup, onLookupSelect, lookupLoading, updateErrors, errorArray, cards=[]} = this.props;
    const selectedState = "differentCard";
    const formStatus = selectedMethod === selectedState ? "block" : "none";
    const manualStatus = manualForm ? "block" : "none";
    const changeForm = manualForm ? false : true;
    const buttonText = cards.length > 0 ? config.differentCard : config.differentCardNoCard;
    return (
      <div id="differentCard">
        <div className="paymentContainer">
          <a
            href="#differentCard"
            className={selectedMethod === selectedState ? "payment--new-card method--active" : "payment--new-card"}
            onClick={e => {
              onCardSelect(selectedState)
              if (addressValues.paymentCardId.length > 0) {
                onFormChange("paymentCardId", "")
              }
            }}
            data-testid='payWithCard'
            >
              {buttonText}
              <Icon classes="ln-c-icon" icon="credit-card" stroke="transparent" />
          </a>
          <div
            className="paymentForm"
            style={{display: formStatus}}
            >
              <div className="editBillingAddress isAddAddresss">
                <div className="form_field-label">
                  <div className="checkout-section-subheader">
                    <h3>{config.billingAddress}</h3>
                  </div>
                </div>
                <div className="clearfix">
                  <BillingAddress
                    manualForm={manualForm}
                    errorArray={errorArray}
                    multiFormChange={multiFormChange}
                    addressValues={addressValues}
                    onFormChange={onFormChange}
                    onAddressLookup={() => onAddressLookup(addressValues, errorArray)}
                    addressLookupData={addressLookupData}
                    addressLookupCalled={addressLookupCalled}
                    onLookupSelect={(res) => onLookupSelect(res, addressValues, errorArray)}
                    lookupLoading={lookupLoading}
                    isError={(val, result, id) => {
                        const createErrors = isError(result, id, errorArray);
                        updateErrors(createErrors);
                      }
                    }
                  />
                </div>

                <div className="clearfix">
                  <div className="showHideDiv">
                    <a href="#" onClick={e => {
                      e.preventDefault()
                      onShowManualForm(changeForm)
                    }}>
                      {changeForm ? "Enter address manually" : "Search for your address with a postcode"}
                    </a>
                  </div>
                  <div style={{display: manualStatus}}>
                    <ManualForm
                      errorArray={errorArray}
                      addressValues={addressValues}
                      onFormChange={onFormChange}
                      isError={(val, result, id) => {
                          const createErrors = isError(result, id, errorArray, val.length);
                          updateErrors(createErrors);
                        }
                      }
                    />
                  </div>
                </div>
              </div>
          </div>
        </div>
      </div>
    );
  }
};

DifferentCard.propTypes = {
  selectedMethod: PropTypes.string.isRequired,
  addressValues: PropTypes.object.isRequired,
  manualForm: PropTypes.bool.isRequired,
  addressLookupData: PropTypes.array.isRequired,
  addressLookupCalled: PropTypes.bool.isRequired,
  onCardSelect: PropTypes.func.isRequired,
  onFormChange: PropTypes.func.isRequired,
  multiFormChange: PropTypes.func.isRequired,
  onAddressLookup: PropTypes.func.isRequired,
  onShowManualForm: PropTypes.func.isRequired,
  onLookupSelect: PropTypes.func.isRequired,
  lookupLoading: PropTypes.bool.isRequired,
  cards: PropTypes.array.isRequired,
  shippingAddressLoaded: PropTypes.bool.isRequired,
  onUpdateShippingAddress: PropTypes.func.isRequired
};

export default DifferentCard;
