const shippingService = (function() {
  return {
    $container: document.getElementById('paymentDetails'),
    useAddress: function() {
      const useStatus = this.$container.dataset.useshipping;
      return JSON.parse(useStatus);
    },
    isClickCollect: function() {
      const collectStatus = this.$container.dataset.clickcollect;
      return JSON.parse(collectStatus);
    },
    clickcollectMerchant: function() {
      const collectMerchant = this.$container.dataset.clickcollectmerchant;
      return collectMerchant ? collectMerchant : "";
    },
    returnAddress: function() {
      const getAddress = this.$container.dataset.shippingaddress;
      return JSON.parse(getAddress);
    },
    createAddressObject: function() {
      const addresses = this.returnAddress();
      const setObject = {
        addressDeliveryLine1: addresses.line1,
        addressDeliveryLine2: addresses.line2 ? addresses.line2 : "",
        addressDeliveryTownCity: addresses.townCity,
        addressDeliveryhouseNameOrNumber: addresses.houseNameOrNumber,
        addressPostcode: addresses.postcode,
        newContactNumber: addresses.contactNumber ? addresses.contactNumber : "",
        newFirstName: addresses.firstName,
        newSurname: addresses.lastName,
        newTitle: addresses.titleCode
      };
      return setObject;
    },
    stubDataObject: function() {
      const setObject = {
        addressDeliveryLine1: "stubbed line1",
        addressDeliveryLine2: "stubbed line2",
        addressDeliveryTownCity: "stubbed town",
        addressDeliveryhouseNameOrNumber: "999",
        addressPostcode: "W1 2TG",
        newContactNumber: "07761 123456",
        newFirstName: "stubbed name",
        newSurname: "stubbed surname",
        newTitle: "mr"
      };
      return setObject;
    },
    paypalAddressObject: function() {
      const addresses = this.returnAddress();
      const clickcollect = this.isClickCollect();
      const clickcollectMerchant = this.clickcollectMerchant();
      const line2 = {
        line2: addresses.line2 ? addresses.line2 : "",
      };

      const setObject = {
        recipientName: clickcollect ? `S2S ${clickcollectMerchant}` : `${addresses.titleCode} ${addresses.firstName} ${addresses.lastName}`,
        line1: addresses.houseNameOrNumber ? `${addresses.houseNameOrNumber} ${addresses.line1}` : addresses.line1,
        city: addresses.townCity,
        postalCode: addresses.postcode,
        countryCode: 'GB'
      };

      return clickcollect ? setObject : Object.assign({}, line2, setObject);
    },
    paypalBillingObject: function(data={}) {
      const setObject = {
        addressDeliveryLine1: data.billingAddress.line1,
        addressDeliveryLine2: data.billingAddress.line2,
        addressDeliveryTownCity: data.billingAddress.city,
        addressDeliveryhouseNameOrNumber: data.billingAddress.line1,
        addressPostcode: data.billingAddress.postalCode,
        newFirstName: data.firstName,
        newSurname: data.lastName,
        newTitle: 'mr'
      }

      return setObject
    }
  };
}());

export default shippingService;
