import React from 'react';
import PropTypes from 'prop-types';
import TextInput from './TextInput';
import Select from './Select';
import config from '../config/config';
import errors from '../../../config/errors';

const manualForm = ({ addressValues, onFormChange, isError,
  errorArray, showCountry, countries }) => {
  return (
    <div>
      <TextInput
        id={config.formFields.addressLine1.id}
        mandatory={config.formFields.addressLine1.mandatory}
        label={config.formFields.addressLine1.label}
        required={config.formFields.addressLine1.required}
        autocomplete={config.formFields.addressLine1.autocomplete}
        maxlength={config.formFields.addressLine1.maxlength}
        value={addressValues[config.formFields.addressLine1.id]}
        changeForm={(id, value) => onFormChange(id, value)}
        errorMessage={errors.messages[config.formFields.addressLine1.id]}
        errorReg={errors.regexPatterns['alphanumeric']}
        errorArray={errorArray}
        isError={(val, result, id) => isError(val, result, id)}
      />

      <TextInput
        id={config.formFields.addressLine2.id}
        label={config.formFields.addressLine2.label}
        required={config.formFields.addressLine2.required}
        autocomplete={config.formFields.addressLine2.autocomplete}
        maxlength={config.formFields.addressLine2.maxlength}
        value={addressValues[config.formFields.addressLine2.id]}
        changeForm={(id, value) => onFormChange(id, value)}
        errorMessage={errors.messages[config.formFields.addressLine2.id]}
        errorReg={errors.regexPatterns['alphanumeric']}
        errorArray={errorArray}
        isError={(val, result, id) => isError(val, result, id)}
      />

      <TextInput
        id={config.formFields.addressDeliveryTownCity.id}
        label={config.formFields.addressDeliveryTownCity.label}
        required={config.formFields.addressDeliveryTownCity.required}
        autocomplete={config.formFields.addressDeliveryTownCity.autocomplete}
        mandatory={config.formFields.addressDeliveryTownCity.mandatory}
        maxlength={config.formFields.addressDeliveryTownCity.maxlength}
        value={addressValues[config.formFields.addressDeliveryTownCity.id]}
        changeForm={(id, value) => onFormChange(id, value)}
        errorMessage={errors.messages[config.formFields.addressDeliveryTownCity.id]}
        errorReg={errors.regexPatterns['alphanumeric']}
        errorArray={errorArray}
        isError={(val, result, id) => isError(val, result, id)}
      />

      {
        showCountry && <Select
          id={config.formFields.country.id}
          mandatory={config.formFields.country.mandatory}
          label={config.formFields.country.label}
          required={config.formFields.country.required}
          options={countries}
          value={addressValues[config.formFields.country.id]}
          type="standard"
          onSelect={e => {
            isError(e.target.value, true, config.formFields.country.id)
            onFormChange(config.formFields.country.id, e.target.value)
          }}
        />
      }
    </div>
  );
};

manualForm.propTypes = {
  addressValues: PropTypes.object.isRequired,
  onFormChange: PropTypes.func.isRequired,
  isError: PropTypes.func.isRequired,
  errorArray: PropTypes.array.isRequired,
  countries: PropTypes.array.isRequired
};

export default manualForm;
