const getColleague = (function () {
  return {
    $container: document.getElementById('paymentDetails'),
    init: function () {
      if (this.$container){
        const dataset = this.$container.dataset.colleague;
        const data = dataset.length === 0 ? false : JSON.parse(this.$container.dataset.colleague);
        return typeof data !== 'boolean' ? false : data;
      } else {
        return false;
      }
    }
  };
}());

export default getColleague;
