import React from 'react';
import PropTypes from 'prop-types';
import Card from './CardItems';
import isError from '../actions/isError';
import config from '../config/config';

class DisplayCards extends React.Component {

  constructor(props){
    super(props);
  }

  render(){
    const {cards=[], displayForm, onDisplayForm, selectedMethod, addressValues, addressLookupData, manualForm, multiFormChange, onCardSelect, onFormChange, onShowManualForm, onAddressLookup, onLookupSelect, lookupLoading, updateErrors, errorArray, addressLookupCalled} = this.props;

    return (
      <div>
        {cards.length > 0 &&
        <div className="ln-u-display-block details-header">
          <h3>{config.savedHeader}</h3>
        </div>
        }
        <div className="ln-u-display-block">
          {cards.length > 0 &&
          <ul className="customer-options">
            {cards.map((card, index) => (
              <Card
                key={card.id}
                selectedMethod={selectedMethod}
                displayForm={displayForm}
                onDisplayClick={(res) => onDisplayForm(res)}
                onItemChange={() => onCardSelect("savedCards")}
                addressValues={addressValues}
                onFormChange={onFormChange}
                onAddressLookup={() => onAddressLookup(addressValues, errorArray)}
                addressLookupData={addressLookupData}
                onLookupSelect={(res) => onLookupSelect(res, addressValues, errorArray)}
                lookupLoading={lookupLoading}
                addressLookupCalled={addressLookupCalled}
                manualForm={manualForm}
                onShowManualForm={onShowManualForm}
                multiFormChange={multiFormChange}
                isError={(val, result, id) => {
                    const createErrors = isError(result, id, errorArray);
                    updateErrors(createErrors);
                  }
                }
                errorArray={errorArray}
                {...card}
                index={index}
               />
            ))}
          </ul>
          }
        </div>
      </div>
    );
  }
};

DisplayCards.propTypes = {
  cards: PropTypes.array.isRequired,
  onCardSelect: PropTypes.func.isRequired,
  onDisplayForm: PropTypes.func.isRequired,
  selectedMethod: PropTypes.string.isRequired,
  displayForm: PropTypes.bool.isRequired,
  addressValues: PropTypes.object.isRequired,
  manualForm: PropTypes.bool.isRequired,
  addressLookupData: PropTypes.array.isRequired,
  onFormChange: PropTypes.func.isRequired,
  multiFormChange: PropTypes.func.isRequired,
  onAddressLookup: PropTypes.func.isRequired,
  onShowManualForm: PropTypes.func.isRequired,
  onLookupSelect: PropTypes.func.isRequired,
  lookupLoading: PropTypes.bool.isRequired,
  updateErrors: PropTypes.func.isRequired,
  errorArray: PropTypes.array.isRequired
};

export default DisplayCards;
