import React from 'react';
import PropTypes from 'prop-types';

const Address = ({houseNameOrNumber, line1, line2, postalCode, town}) => {
  return (
    <p data-testid="address">
      <strong>{houseNameOrNumber} {line1}</strong>,<br />
      {line2.length > 0 &&
        <span>
          {line2},<br />
        </span>
      }
      {town},<br />

      <span className="text--uppercase">{postalCode}</span>
    </p>
  );
};

Address.propTypes = {
  houseNameOrNumber: PropTypes.string.isRequired,
  line1: PropTypes.string.isRequired,
  line2: PropTypes.string,
  postalCode: PropTypes.string.isRequired,
  town: PropTypes.string.isRequired,
};

export default Address;
