import React from 'react';
import PropTypes from 'prop-types';
import Icon from './Icon';
import config from '../config/config';

const AlternativeCard = ({ selectedMethod, addressValues, paypalButtonStatus, onCardSelect, onFormChange}) => {
  const selectedState = "alternativeCard";
  const formStatus = selectedMethod === selectedState ? "block" : "none";

  return (
    <section>
      <div className="ln-u-display-inline-block ln-u-1/1">
        <div className="ln-u-display-block details-header">
          <h3>{config.alternativeHeader}</h3>
        </div>
        <div className={`paymentContainer ln-c-form-group ${paypalButtonStatus === 'haserror' ? 'has-error' : ''}`} id="alternativeCard">
          <a
            href="#alternativeCard"
            data-testid="payWithPayPal"
            className={selectedMethod === selectedState && paypalButtonStatus !== 'haserror' ? "payment--new-card method--active" : "payment--new-card"}
            onClick={e => {
              e.preventDefault();
              onCardSelect(selectedState)
              if (addressValues.paymentCardId.length > 0) {
                onFormChange("paymentCardId", "")
              }
            }}
            >
            {config.payPalButton}
            <Icon classes="ln-c-icon ln-c-icon--paypal" icon="paypal" stroke="transparent" />
          </a>

          {paypalButtonStatus === 'haserror' &&
            <div className="ln-c-field-info ln-c-field-info--error tu-c-field-info--paypal-error ln-u-1/1">
              <div className="ln-u-display-block ln-u-soft-sides">
                <h4 className="ln-u-h4 tu-u-color-red ln-u-flush-bottom">{config.errorHeader}</h4>
                <p className="ln-u-h5 tu-u-color-black ln-u-flush-bottom">{config.errorText}</p>
              </div>
            </div>
          }
        </div>
      </div>
    </section>
  );
};

AlternativeCard.propTypes = {
  selectedMethod: PropTypes.string.isRequired,
  addressValues: PropTypes.object.isRequired,
  paypalButtonStatus: PropTypes.string.isRequired,
  onCardSelect: PropTypes.func.isRequired,
  onFormChange: PropTypes.func.isRequired
};

export default AlternativeCard;
