import React from 'react';
import PropTypes from 'prop-types';
import Select from './Select';
import TextInput from './TextInput';
import AddressLookup from './AddressLookup';
import config from '../config/config';
import errors from '../../../config/errors';

const BillingAddress = ({ addressValues, onFormChange, multiFormChange,
  onAddressLookup, addressLookupData, addressLookupCalled,
  onLookupSelect, lookupLoading, isError, errorArray, validate, enableFindAddress }) => {
  return (
    <div>
      <Select
        id={config.formFields.title.id}
        mandatory={config.formFields.title.mandatory}
        label={config.formFields.title.label}
        required={config.formFields.title.required}
        options={config.formFields.title.options}
        value={addressValues[config.formFields.title.id]}
        type="standard"
        onSelect={e => {
          isError(e.target.value, true, config.formFields.title.id)
          onFormChange(config.formFields.title.id, e.target.value)
        }}
      />
      <TextInput
        id={config.formFields.firstname.id}
        mandatory={config.formFields.firstname.mandatory}
        label={config.formFields.firstname.label}
        required={config.formFields.firstname.required}
        autocomplete={config.formFields.firstname.autocomplete}
        maxlength={config.formFields.firstname.maxlength}
        value={addressValues[config.formFields.firstname.id]}
        changeForm={(id, value) => onFormChange(id, value)}
        errorMessage={errors.messages[config.formFields.firstname.id]}
        errorReg={errors.regexPatterns['alpha']}
        errorArray={errorArray}
        isError={(val, result, id) => isError(val, result, id)}
        dataTestid="firstName"
       />
      <TextInput
        id={config.formFields.surname.id}
        mandatory={config.formFields.surname.mandatory}
        label={config.formFields.surname.label}
        required={config.formFields.surname.required}
        autocomplete={config.formFields.surname.autocomplete}
        maxlength={config.formFields.surname.maxlength}
        value={addressValues[config.formFields.surname.id]}
        changeForm={(id, value) => onFormChange(id, value)}
        errorMessage={errors.messages[config.formFields.surname.id]}
        errorReg={errors.regexPatterns['alpha']}
        errorArray={errorArray}
        isError={(val, result, id) => isError(val, result, id)}
        dataTestid="surname"
      />
      <TextInput
        id={config.formFields.contact.id}
        label={config.formFields.contact.label}
        required={config.formFields.contact.required}
        autocomplete={config.formFields.contact.autocomplete}
        value={addressValues[config.formFields.contact.id]}
        changeForm={(id, value) => onFormChange(id, value)}
        errorMessage={errors.messages[config.formFields.contact.id]}
        errorReg={errors.regexPatterns['mobile']}
        errorArray={errorArray}
        isError={(val, result, id) => isError(val, result, id)}
      />
      <hr className="addessLookupDivide"/>
      <TextInput
        id={config.formFields.houseNumber.id}
        mandatory={config.formFields.houseNumber.mandatory}
        label={config.formFields.houseNumber.label}
        required={config.formFields.houseNumber.required}
        autocomplete={config.formFields.houseNumber.autocomplete}
        maxlength={config.formFields.houseNumber.maxlength}
        value={addressValues[config.formFields.houseNumber.id]}
        changeForm={(id, value) => onFormChange(id, value)}
        errorMessage={errors.messages[config.formFields.houseNumber.id]}
        errorReg={errors.regexPatterns['alphanumeric']}
        errorArray={errorArray}
        isError={(val, result, id) => isError(val, result, id)}
        dataTestid="houseNumber"
      />
      <AddressLookup
        id={config.formFields.postcode.id}
        enableFindAddress={enableFindAddress}
        mandatory={config.formFields.postcode.mandatory}
        label={config.formFields.postcode.label}
        required={config.formFields.postcode.required}
        autocomplete={config.formFields.postcode.autocomplete}
        maxlength={config.formFields.postcode.maxlength}
        button={config.formFields.postcode.button}
        values={addressValues}
        value={addressValues[config.formFields.postcode.id]}
        changeForm={(id, value) => onFormChange(id, value)}
        onAddressLookup={onAddressLookup}
        addressLookupData={addressLookupData}
        addressLookupCalled={addressLookupCalled}
        lookupSelect={(res) => onLookupSelect(res)}
        setloading={lookupLoading}
        errorMessage={errors.messages[config.formFields.postcode.id]}
        errorReg={validate ? errors.regexPatterns['postcode'] : errors.regexPatterns['alphanumeric']}
        errorArray={errorArray}
        isError={(val, result, id) => isError(val, result, id)}
      />
  </div>
  );
};

BillingAddress.propTypes = {
  addressValues: PropTypes.object.isRequired,
  addressLookupData: PropTypes.array.isRequired,
  addressLookupCalled: PropTypes.bool.isRequired,
  onFormChange: PropTypes.func.isRequired,
  onAddressLookup: PropTypes.func.isRequired,
  onLookupSelect: PropTypes.func.isRequired,
  lookupLoading: PropTypes.bool.isRequired,
  multiFormChange: PropTypes.func.isRequired,
  isError: PropTypes.func.isRequired,
  errorArray: PropTypes.array.isRequired,
  validate: PropTypes.bool.isRequired,
  enableFindAddress: PropTypes.bool.isRequired
};

export default BillingAddress;
