import axios from 'axios';
import config from '../config/config';

const cpmService = (function () {
  return {
    $cpmGETURL: '/my-account/marketing-preferences-json',
    $cpmGuestGETURL: '/checkout/multi/payment-method/tu-marketing-preferences-json',
    $cpmRegisterGETURL: '/login/tu-marketing-preferences-json',
    getContainer: function () { return document.getElementById('CPM-account'); },
    getPaymentContainer: function () { return document.getElementById('paymentDetails'); },
    getIsCPM: function () {
      const paymentContainer = this.getPaymentContainer();
      if (paymentContainer) {
        const data = paymentContainer.dataset.iscpm;
        return JSON.parse(data);
      } else {
        return false;
      }
    },
    getCSRF: function () {
      const container = this.getContainer();
      if (container) {
        const data = container.dataset.csrf;
        return data;
      } else {
        return false;
      }
    },
    getCPMData: function (url='$cpmGETURL', params={}) {
      return new Promise((resolve, reject) => {
        axios.get(this[url], { params: params })
          .then((response) => {
            resolve(response.data);
          })
          .catch(() => {
            resolve(null);
          });
      });
    },
    sendCPMData: function (url='$cpmGETURL', data={}) {
      return new Promise((resolve, reject) => {
        axios.post(this[url], data, {
          params: {
            "CSRFToken": cpmService.getCSRF()
          }
        })
        .then((response) => {
          if( response.status === 200 ) {
            resolve(true);
          }
        })
        .catch((error) => {
          reject(false);
        });
      });
    },
    createCPMObject: function (data) {
      let cpmObject = config.marketingOptions.slice();

      cpmObject.map(item => {
        if (data.hasOwnProperty(item.name)) {
          item.value = (data[item.name] === null || data[item.name].length === 0) ? null : data[item.name].toString();
      	}
      });

      return cpmObject;
    }
  };
}());

export default cpmService;
