export const addCards = (cards = []) => {
  return {
    type: 'ADDED_CARDS',
    cards: cards
  }
}

export const paymentMethod = (selected = "empty") => {
  return {
    type: 'SET_PAYMENT_METHOD',
    selected
  }
}

export const formDisplay = (display = false) => {
  return {
    type: 'SET_DISPLAY_FORM',
    display
  }
}

export const showManualMethod = (display = false) => {
  return {
    type: 'SET_SHOW_MANUAL_FORM',
    display
  }
}

export const errorsMethod = (errors = []) => {
  return {
    type: 'UPDATE_ERRORS',
    errors
  }
}

export const addressMethod = (name, value) => {
  return {
    type: 'UPDATE_ADDRESS_FORM',
    name, value
  }
}

export const shippingAddressLoaded = (loaded = false) => {
  return {
    type: 'SET_SHIPPING_ADDRESS',
    loaded
  }
}

export const multiAddressMethod = (values) => {
  return {
    type: 'UPDATE_ADDRESSES',
    values
  }
}

export const addressLookupMethod = (addresses) => {
  return {
    type: 'SET_LOOKUP_CONTENT',
    addresses: addresses,
    lookupCalled: true
  }
}

export const lookupLoadMethod = (status) => {
  return {
    type: 'SET_LOOKUP_LOAD',
    status
  }
}

export const setPaypalMethod = (status) => {
  return {
    type: 'SET_PAYPAL_STATUS',
    status
  }
}

export const setTokenMethod = (token) => {
  return {
    type: 'SET_PAYPAL_TOKEN',
    token
  }
}

export const setNonceMethod = (nonce) => {
  return {
    type: 'SET_PAYPAL_NONCE',
    nonce
  }
}

export const setValidation = (validate) => {
  return {
    type: 'SET_VALIDATION',
    validate
  }
}

export const showCountry = (showCountry) => {
  return {
    type: 'SHOW_COUNTRY',
    showCountry
  }
}

export const setCountries = (countries) => {
  return {
    type: 'SET_COUNTRIES',
    countries
  }
}
