import { connect } from 'react-redux';
import BillingAddress from '../presentational/BillingAddress';
import * as util from '../util';

const mapStateToProps = (state, ownProps) => {
  const { lookupLoading: loading, manualForm } = ownProps;
  // If country is 'UK', validate is true
  const { validate } = state.formData;
  const isLookUpEnabled = util.getLookUpEnabledFlag({ manualForm, isCountryUK: validate });
  const enableFindAddress = util.getEnableFindAddressFlag({ loading, isLookUpEnabled });
  return {
    validate,
    enableFindAddress
  };
};

const BillingAddressContainer = connect(mapStateToProps)(BillingAddress);

export default BillingAddressContainer;
