const lookupErrors = (values={}, errors=[]) => {
  let newErrors = errors.slice();
  for (let key in values) {
    if(values.hasOwnProperty(key) && values[key] !== '' && errors.indexOf(key) >= 0) {
      const index = newErrors.indexOf(key);
      newErrors.splice(index, 1);
    }
  }

  return newErrors
};

export default lookupErrors;
