/* global paypal,$ */
import client from 'braintree-web/client';
import paypalCheckout from 'braintree-web/paypal-checkout';
import React from 'react';
import { connect } from 'react-redux';
import paypalConfig from '../config/paypal';
import { setPaypalMethod } from './index';

const paypalButtonStr = 'paypal-button';
const paypalButton = document.getElementById(paypalButtonStr);

const paypalService = (function () {
  return {
    $authURL: '/checkout/multi/paypal/token',
    $nonceURL: '/checkout/multi/paypal/noncetoken',
    _getPrice: function () {
      const formattedPrice = document.getElementById('paymentDetails').dataset.price.replace(/[^\d.-]/g, '');
      return parseFloat(formattedPrice);
    },
    getPayPalStatus: function () {
      const container = document.getElementById('paymentDetails');
      const paypalStatus = container.dataset.showpaypal;
      if (paypalStatus){
        return JSON.parse(container.dataset.showpaypal);
      } else {
        return false;
      }
    },
    _logErr: function (error) {
      console.log(error);
    },

    init: function () {
      if (paypalButton) {
        this.getToken();
      }
    }
  };
}());

export default paypalService;
