import React from 'react';
import PropTypes from 'prop-types';
import config from '../config/config';
import cpmService from '../../CPM/actions/cpmupdate';
import getCSRF from '../actions/getCSRF';
import isError from '../actions/isError';
import setFormUrl from '../actions/setFormUrl';
import marketingStatus from '../actions/marketingStatus';
import HiddenForm from './HiddenForm';
import MarketingText from '../../CPM/presentational/MarketingText';

class TermsAndConditions extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      cpmdataupdated: false,
      cpminit: null,
      cpmdata: null
    };
  }

  componentWillMount() {
    if (!this.state.cpmdataupdated) {
      if (cpmService.getIsCPM()) {
        cpmService.getCPMData('$cpmGuestGETURL').then((data) => {
          if (data !== null) {
            this.setState({
              cpmdataupdated: true,
              cpminit: data.toString(),
              cpmdata: (data === null || data.length === 0) ? 'li' : data.toString()
            });
          }
        });
      }
      else {
        this.setState({
          cpmdataupdated: true
        });
      }
    }
  }

  componentDidUpdate() {
    if (getCSRF.getEnv()) {
      if (this.props.selectedMethod === 'alternativeCard' && this.props.paypalButtonStatus === 'unloaded') {
        this.props.setStubData(this.props.addressValues, this.props.errorArray);
      }
    }
    else {
      if (this.props.selectedMethod === 'alternativeCard' && this.props.paypalButtonStatus === 'unloaded') {
        this.props.paypalStatus('loading');
      }

      if (this.props.selectedMethod === 'alternativeCard' && this.props.paypalButtonStatus === 'loading' && this.props.paypalToken.length === 0) {
        this.props.setToken();
      }

      if (this.props.selectedMethod === 'alternativeCard' && this.props.paypalButtonStatus === 'loading' && this.props.paypalToken.length > 0) {
        this.props.initiateButton(this.props.paypalToken, this.props.addressValues);
      }

      if (this.props.selectedMethod === 'alternativeCard' && this.props.paypalButtonStatus === 'authorized' && this.props.paypalNonce.length > 0) {
        this.refs.paymentForm.submit();
      }
    }
  }

  render(){
    const {selectedMethod, addressValues, csrf, paypalButtonStatus, paypalToken, paypalNonce, onFormChange, errorArray, updateErrors, paypalStatus, setToken, initiateButton, isColleague} = this.props;
    const buttonStatus = (paypalButtonStatus === 'stubComplete' && errorArray.length === 0 && selectedMethod === 'alternativeCard') || (selectedMethod !== '' && errorArray.length === 0 && selectedMethod !== 'alternativeCard') || (selectedMethod === 'savedCards' && addressValues.paymentCardId.length > 0 && addressValues.termsAndConditionsId) ? false : true;
    const colleagueContent = isColleague ? 'and colleague discount terms and conditions' : '';
    const buttonClass = (paypalButtonStatus === 'loading' || paypalButtonStatus === 'initiated') && addressValues.termsAndConditionsId ? 'ln-c-button ln-c-button--primary ln-c-button--loading' : 'ln-c-button ln-c-button--primary';
    const formUrl = setFormUrl(selectedMethod, errorArray.length);

    return (
      <form id="tuPaymentDetailsForm" className="checkoutForm" action={formUrl} method="post" ref="paymentForm">

        <div className="tandc-card">
          <HiddenForm cpmdata={this.state.cpmdata} addressValues={addressValues} csrf={csrf} paypalToken={paypalToken}
                      paypalNonce={paypalNonce} isColleague={isColleague} errorArray={errorArray}/>
          {marketingStatus.getStatus() === true && this.state.cpminit !== 'li' && this.state.cpminit !== 'true' && this.state.cpmdataupdated === true &&
          <MarketingText
            cpmdata={this.state.cpmdata}
            subheader={this.state.cpminit === 'false' ? 'subheader' : 'subheaderunknown'}
            subheader2={this.state.cpminit === 'false' ? 'subheader2' : 'subheader2unknown'}
            label={this.state.cpminit === 'false' ? 'label' : 'labelunknown'}
            isCPMOn={cpmService.getIsCPM()}
            cpminit={this.state.cpminit}
            setCPMData={(val) => {
              this.setState(prevState => ({
                cpmdata: val
              }));
            }}
          />
          }
          <div className="ln-c-form-group ln-u-push-bottom">
            <h3 className="ln-u-h6">{config.formFields.termsAndConditions.label} *</h3>
            <div className="ln-c-form-option ln-c-form-option--checkbox">
              <input
                className="ln-c-form-option__input"
                id={config.formFields.termsAndConditions.id}
                name="tuTermsAndConditions.termsAndConditions"
                type="checkbox"
                required={config.formFields.termsAndConditions.required}
                value={addressValues.termsAndConditionsId}
                checked={addressValues.termsAndConditionsId ? 'checked' : null}
                onChange={e => {

                  onFormChange(config.formFields.termsAndConditions.id, !addressValues.termsAndConditionsId);
                  const createErrors = isError(!addressValues.termsAndConditionsId, config.formFields.termsAndConditions.id, errorArray);
                  updateErrors(createErrors);
                }}
              />
              <label
                className="ln-c-form-option__label"
                htmlFor={config.formFields.termsAndConditions.id}
                onClick={e => {
                  onFormChange(config.formFields.termsAndConditions.id, !addressValues.termsAndConditionsId);
                  const createErrors = isError(!addressValues.termsAndConditionsId, config.formFields.termsAndConditions.id, errorArray);
                  updateErrors(createErrors);
                }}
                data-testid="termsAndConditions">
                 I accept the&nbsp;
              </label> 
              
              <span>
                <a href={config.termsAndPrivacy.terms.link} 
                                rel='noopener noreferrer'>{config.termsAndPrivacy.terms.linkText}</a> and <a
                href={config.termsAndPrivacy.privacy.link}
                rel='noopener noreferrer'>{config.termsAndPrivacy.privacy.linkText}</a> {colleagueContent}
              </span>
            </div>
          </div>
          <div className="ln-u-text-align-center">
            <button
              id="contPayment"
              className={buttonClass}
              type="submit"
              value="Submit"
              disabled={buttonStatus}
              data-testid="continueToPayment"
              style={{display: (selectedMethod === 'alternativeCard' && paypalButtonStatus === 'loaded' && addressValues.termsAndConditionsId) ? 'none' : 'inline-block'}}
            >
              {config.submitButton}
            </button>
            <div className="form_field-input paypal-container"
                 style={{display: (selectedMethod === 'alternativeCard' && paypalButtonStatus === 'loaded' && addressValues.termsAndConditionsId) ? 'inline-block' : 'none'}}>
              <div id="paypal-button" data-testid="paypalContinue" />
            </div>
          </div>
        </div>
      </form>
    )
  }
}

TermsAndConditions.propTypes = {
  selectedMethod: PropTypes.string.isRequired,
  addressValues: PropTypes.object.isRequired,
  paypalButtonStatus: PropTypes.string.isRequired,
  paypalToken: PropTypes.string.isRequired,
  paypalNonce: PropTypes.string.isRequired,
  onFormChange: PropTypes.func.isRequired,
  updateErrors: PropTypes.func.isRequired,
  paypalStatus: PropTypes.func.isRequired,
  setToken: PropTypes.func.isRequired,
  initiateButton: PropTypes.func.isRequired,
  errorArray: PropTypes.array.isRequired,
  isColleague: PropTypes.bool
};

export default TermsAndConditions;
