import React from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import Cards from '../presentational/Cards';
import cardService from '../actions/getCards';
import mergeValues from '../actions/mergeValues';
import lookupErrors from '../actions/lookupErrors';
import createLookupObject from '../actions/createLookupObject';
import {
  paymentMethod, addressMethod, multiAddressMethod, addressLookupMethod,
  showManualMethod, lookupLoadMethod, errorsMethod, formDisplay, setValidation
} from '../actions/index';

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    onCardSelect: (text) => {
      dispatch(paymentMethod(text))
    },
    onFormChange: (name, value) => {
      if (name === 'country') {
        if (value === 'GB') {
          dispatch(setValidation(true));
        } else {
          dispatch(setValidation(false));
        }
      }
      dispatch(addressMethod(name, value))
    },
    multiFormChange: (values) => {
      dispatch(multiAddressMethod(values));
    },
    onShowManualForm: (display) => {
      dispatch(showManualMethod(display));
      // Fetch countries on ManualForm load
      ownProps.onManualFormLoad();
    },
    updateErrors: (errors) => {
      dispatch(errorsMethod(errors))
    },
    onAddressLookup: (values={}, errors=[]) => {
      const url = `/checkout/multi/home-delivery/find-address-json?postcode=${values.addressPostcode}&houseNameOrNumber=${values.addressDeliveryhouseNameOrNumber}`;
      dispatch(lookupLoadMethod(true));
      axios.get(url).then(response => {
        const changeFormItems = response.data.length === 1 ?
          createLookupObject(response.data[0]) : createLookupObject();
        const multiAddress = mergeValues(values, changeFormItems);
        // Set country to 'UK' and enable validation
        multiAddress['country'] = 'GB';
        dispatch(setValidation(true));
        const updatedErrors = lookupErrors(changeFormItems, errors);
        dispatch(addressLookupMethod(response.data));
        dispatch(lookupLoadMethod(false));
        dispatch(multiAddressMethod(multiAddress));

        if (response.data.length === 1) {
          dispatch(errorsMethod(updatedErrors));
        }
      });
    },
    onLookupSelect: (data=[], values={}, errors=[]) => {
      const changeFormItems = createLookupObject(data[0]);
      const multiAddress = mergeValues(values, changeFormItems);
      const updatedErrors = lookupErrors(changeFormItems, errors);
      dispatch(addressLookupMethod(data));
      dispatch(multiAddressMethod(multiAddress));
      dispatch(errorsMethod(updatedErrors));
    },
    onDisplayForm: (value) => {
      dispatch(formDisplay(value))
    }
  }
}

const mapStateToProps = state => {
  return {
    cards: cardService.getObject(),
    displayForm: state.paymentType.displayForm,
    selectedMethod: state.paymentType.selectedMethod,
    addressValues: state.formData.values,
    errorArray: state.formData.errors,
    manualForm: state.formData.manualForm,
    addressLookupData: state.addressLookupData.data,
    addressLookupCalled: state.addressLookupData.lookupCalled,
    lookupLoading: state.addressLookupData.loading
  }
}

let DisplayCards = connect(mapStateToProps, mapDispatchToProps)(Cards)

export default DisplayCards
