const cardService = (function () {
  return {
    $cards: document.getElementById('paymentDetails'),
    getObject: function () {
      if (this.$cards && this.$cards.dataset.payment){
        const data = this.$cards.dataset.payment;
        return JSON.parse(data);
      } else {
        return [];
      }
    }
  };
}());

export default cardService;
