import React from 'react';
import { Provider } from 'react-redux';
import { reduxStoreShape } from '../redux.proptypes';
import PaymentDetails from '../../components/paymentDetails/PaymentApp';

const ProvidedCheckoutStoreLocator = ({ store }) => (
  <Provider store={store}>
    <PaymentDetails />
  </Provider>
);

ProvidedCheckoutStoreLocator.propTypes = {
  store: reduxStoreShape.isRequired
};

export default ProvidedCheckoutStoreLocator;
