const getCSRF = (function () {
  return {
    $container: document.getElementById('paymentDetails'),
    getEnv: function () {
      if (this.$container){
        const data = this.$container.dataset.env;
        return data.toLowerCase().includes("lte");
      } else {
        return false;
      }
    },
    getPaypalEnv: function () {
      if (this.$container){
        const data = this.$container.dataset.env;
        return data.toLowerCase().includes("prod");
      } else {
        return false;
      }
    },
    init: function () {
      if (this.$container){
        const data = this.$container.dataset.csrf;
        return data;
      } else {
        return false;
      }
    }
  };
}());

export default getCSRF;
