import React from 'react';
import PropTypes from 'prop-types';
import MarketingLinks from './MarketingLinks';
import config from '../config/config';

const MarketingText = ({subheader, subheader2, label, cpmdata, cpminit, isCPMOn=true, setCPMData}) => {
  const setSubheader = isCPMOn ? subheader : 'subheadernonCPM';
  const setLabel = isCPMOn ? label : 'labelnonCPM';
  const setInputName = isCPMOn ? 'contactPreferences' : 'contactPreferencesAny';
  const setUnselected = (cpminit === '' || cpminit === null) ? 'li' : cpminit;
  const setSelected = cpminit === 'false' ? 'true' : 'false';
  return (
    <div className="form_field-input ln-u-push-bottom">
      <h3 className="checkout-step-subtitle">{config.stayInTouch.header}</h3>
      <p>{config.stayInTouch[setSubheader]}</p>
      {subheader2 !== '' && isCPMOn === true &&
        <p>{config.stayInTouch[subheader2]}</p>
      }
      { isCPMOn === true &&
        <MarketingLinks />
      }
      <div className="ln-c-form-option ln-c-form-option--checkbox">
        <input
          id={config.stayInTouch.id}
          name={setInputName}
          type="checkbox"
          value={!isCPMOn ? 'true' : cpmdata}
          className="ln-c-form-option__input"
          onChange={e => {
            const changeValue = e.target.checked ? setSelected : setUnselected;
            if (isCPMOn) {
              setCPMData(changeValue)
            }
          }}
        />
        <label className="ln-c-form-option__label" htmlFor={config.stayInTouch.id}>
          {config.stayInTouch[setLabel]}
        </label>
        { isCPMOn === true &&
          <input type="hidden" name='contactPreferencesAny' value={cpmdata} />
        }
      </div>
    </div>
  );
};

MarketingText.propTypes = {
  subheader: PropTypes.string.isRequired,
  subheader2: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  cpmdata: PropTypes.string.isRequired,
  cpminit: PropTypes.string.isRequired,
  isCPMOn: PropTypes.bool,
  setCPMData: PropTypes.func.isRequired
};

export default MarketingText;
