import React from 'react';
import { connect } from 'react-redux';
import AlternativeCard from '../presentational/AlternativeCard';
import mergeValues from '../actions/mergeValues';
import createLookupObject from '../actions/createLookupObject'
import { paymentMethod, addressMethod } from '../actions/index';

const mapDispatchToProps = (dispatch) => {
  return {
    onCardSelect: (text) => {
      dispatch(paymentMethod(text))
    },
    onFormChange: (name, value) => {
      dispatch(addressMethod(name, value))
    }
  }
}

const mapStateToProps = state => {
  return {
    selectedMethod: state.paymentType.selectedMethod,
    addressValues: state.formData.values,
    paypalButtonStatus: state.paymentType.paypal_status
  }
}

let AlternativeCardForm = connect(mapStateToProps, mapDispatchToProps)(AlternativeCard)

export default AlternativeCardForm
