import React from 'react';
import PropTypes from 'prop-types';

const Select = (props) => {
  let isHidden = props.required ? 'ln-c-label__info' : 'ln-c-label__info ln-u-visually-hidden';
  return (
    <div>
      <div className="ln-c-form-group ln-u-push-bottom">
        <label className="ln-c-label" htmlFor={props.id}>{props.label}
          <span className={isHidden} aria-hidden="true">*</span>
        </label>

        <select
          className="ln-c-select"
          id={props.id}
          name={props.id}
          required={props.required}
          value={props.value}
          onChange={e => {
            e.preventDefault()
            props.onSelect(e)
          }}>
          <option>Please select</option>
          {props.options.map((option, index) => {
            const setValue = props.type === "lookup" ? option.houseNameOrNumber : option.value;
            const setTitle = props.type === "lookup" ? `${option.houseNameOrNumber} ${option.line1}` : option.title;
            return (
              <option value={setValue} data-testid={`title-${setValue}`} key={`${setValue}-${index}`}>
                {setTitle}
              </option>
            )
          })}
        </select>
      </div>
    </div>
  );
};

Select.propTypes = {
  id: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  required: PropTypes.bool.isRequired,
  options: PropTypes.array.isRequired,
  mandatory: PropTypes.string,
  value: PropTypes.string.isRequired,
  onSelect: PropTypes.func.isRequired
};

export default Select;
