import React from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import client from 'braintree-web/client';
import paypalCheckout from 'braintree-web/paypal-checkout';
import TermsAndConditions from '../presentational/TermsAndConditions';
import getCSRF from '../actions/getCSRF';
import getColleague from '../actions/getColleague';
import paypalService from '../actions/paypal';
import shippingService from '../actions/useShippingAddress';
import lookupErrors from '../actions/lookupErrors';
import paypalConfig from '../config/paypal';
import mergeValues from '../actions/mergeValues';
import {paymentMethod, addressMethod, errorsMethod, setPaypalMethod, setTokenMethod, setNonceMethod, multiAddressMethod } from '../actions/index';

const mapDispatchToProps = (dispatch) => {
  return {
    onFormChange: (name, value) => {
      dispatch(addressMethod(name, value))
    },
    updateErrors: (errors) => {
      dispatch(errorsMethod(errors))
    },
    paypalStatus: (status) => {
      dispatch(setPaypalMethod(status))
    },
    setStubData: (values, errors=[]) => {
      const stubData = mergeValues(values, shippingService.stubDataObject());
      const updatedErrors = lookupErrors(shippingService.stubDataObject(), errors);
      dispatch(multiAddressMethod(stubData));
      dispatch(errorsMethod(updatedErrors))
      dispatch(setPaypalMethod("stubComplete"));
    },
    setToken: () => {
      const $authURL = '/checkout/multi/paypal/token';
      axios.get($authURL)
        .then((response) => {
          dispatch(setTokenMethod(response.data))
        })
        .catch((error) => {
          paypalService._logErr(error)
          dispatch(setPaypalMethod("haserror"));
          dispatch(paymentMethod(""))
        });
    },
    initiateButton: (token, values) => {
      dispatch(setPaypalMethod('initiated'));
      const price = paypalService._getPrice();
      const shippingAddress = shippingService.paypalAddressObject();
      const environment = getCSRF.getPaypalEnv() === true ? 'production' : 'sandbox';

      client.create({
        authorization: token,
      }, (clientErr, clientInstance) => {
        if (clientErr) {
          paypalService._logErr('Error creating client:', clientErr);
          dispatch(setPaypalMethod("haserror"));
          dispatch(paymentMethod(""))
          return;
        }
        // Create a PayPal Checkout component.
        paypalCheckout.create({
          client: clientInstance
        }, (paypalCheckoutErr, paypalCheckoutInstance) => {
          if (paypalCheckoutErr) {
            paypalService._logErr('Error creating PayPal Checkout:', paypalCheckoutErr);
            dispatch(setPaypalMethod("haserror"));
            dispatch(paymentMethod(""))
            return;
          }

          // Set up PayPal with the checkout.js library
          if (typeof (paypal) !== 'undefined') {
            paypal.Button.render({
              env: environment,
              commit: paypalConfig.paypal.commit,
              locale: paypalConfig.paypal.locale,
              style: paypalConfig.paypal.style,
              payment: function () {
                return paypalCheckoutInstance.createPayment({
                  flow: paypalConfig.paypal.flow,
                  intent: paypalConfig.paypal.intent,
                  displayName: paypalConfig.paypal.displayName,
                  amount: price, // Required
                  currency: paypalConfig.paypal.currency, // Required
                  locale: paypalConfig.paypal.locale,
                  enableShippingAddress: paypalConfig.paypal.enableShippingAddress,
                  shippingAddressEditable: paypalConfig.paypal.shippingAddressEditable,
                  shippingAddressOverride: shippingAddress
                });
              },
              /* eslint-disable */
              onAuthorize: function (data, actions) {
                return paypalCheckoutInstance.tokenizePayment(data).then(function(payload){
                  if( payload.details.firstName ) {
                    const setBillingAddress = shippingService.paypalBillingObject(payload.details);
                    const multiAddress = mergeValues(values, setBillingAddress);
                    dispatch(multiAddressMethod(multiAddress));
                    dispatch(setNonceMethod(payload.nonce))
                    dispatch(setPaypalMethod('authorized'));
                  }
                })
              },

              onCancel: function (data) {
                console.log('checkout.js payment cancelled', JSON.stringify(data, 0, 2));
              },

              onError: function (err) {
                console.log('checkout.js error', err);
                dispatch(setPaypalMethod("haserror"));
                dispatch(paymentMethod(""))
              }
            }, "paypal-button").then(() => {
              dispatch(setPaypalMethod('loaded'))
            });
          } else {
            paypalService._logErr('paypal (checkout.js) not loaded.');
            dispatch(setPaypalMethod("haserror"));
            dispatch(paymentMethod(""))
          }
        });
      });
    }
  }
}

const mapStateToProps = state => {
  return {
    selectedMethod: state.paymentType.selectedMethod,
    addressValues: state.formData.values,
    errorArray: state.formData.errors,
    csrf: getCSRF.init(),
    paypalButtonStatus: state.paymentType.paypal_status,
    paypalToken: state.paymentType.paypal_token,
    paypalNonce: state.paymentType.paypal_nonce,
    isColleague: getColleague.init()
  }
}

let TermsAndConditionsForm = connect(mapStateToProps, mapDispatchToProps)(TermsAndConditions)

export default TermsAndConditionsForm
