import React from 'react';
import PropTypes from 'prop-types';
import config from '../config/config';

const HiddenForm = ({addressValues, csrf, paypalNonce, paypalToken, isColleague, errorArray}) => {
  return (
    <div>
      {Object.keys(addressValues).map(key =>
        <input
          key={key}
          type="hidden"
          value={addressValues[key]}
          id={key}
          name={key}
         />
      )}

      {Object.keys(config.formFields.billingAddress).map(key =>
        <input
          key={key}
          type="hidden"
          value={addressValues[config.formFields.billingAddress[key]]}
          id={key}
          name={key}
         />
      )}

      <input type="hidden" name="nonce" value={paypalNonce} />
      <input type="hidden" name="token" value={paypalToken} />
      <input type="hidden" name="billingAddress.countryIso" value={addressValues.country ? addressValues.country : "GB"} />
      <input type="hidden" name="existingAddressChosen" value={addressValues.paymentCardId.length > 0 && errorArray.length > 0 ? true : false} />
      <input type="hidden" name="_tuTermsAndConditions.termsAndConditions" value="on" />
      <input type="hidden" name="paymentId" value={addressValues.paymentCardId} />
      <input id="CSRFToken" name="CSRFToken" value={csrf} type="hidden" />

      {isColleague === true &&
        <input type="hidden" id="colleguesTermsAndConditionsId" name="tuTermsAndConditions.colleagueTermsAndConditions" value={addressValues.termsAndConditionsId} />
      }
    </div>
  );
};

HiddenForm.propTypes = {
  addressValues: PropTypes.object.isRequired,
  errorArray: PropTypes.array.isRequired,
  csrf: PropTypes.string.isRequired,
  paypalNonce: PropTypes.string.isRequired,
  paypalToken: PropTypes.string.isRequired,
  isColleague: PropTypes.bool.isRequired
};

export default HiddenForm;
