const isError = (results, id, errorArray=[]) => {
  let newErrors = errorArray.slice();
  if ((!results && !errorArray.includes(id))) {
    newErrors.push(id)
  } else if (results && errorArray.includes(id)) {
    newErrors = newErrors.filter(e => e !== id);
  }
  return newErrors
}

export default isError;
