import React from 'react';
import PropTypes from 'prop-types';
import Select from './Select';
import Address from './Address';
import config from '../config/config';
import errors from '../../../config/errors';

class AddressLookup extends React.Component {

  constructor(props) {
    super(props);
    const { value, errorReg } = props;
    this.state = { errorPassed: value.length > 0 ? errorReg.test(value) : true }
  }

  static getDerivedStateFromProps(props) {
    const { value, errorReg } = props;
    return { errorPassed: value.length > 0 ? errorReg.test(value) : true };
  }

  componentDidUpdate(prevProps, prevState) {
    const { isError, value, required, errorReg, id } = this.props;
    if (prevState.errorPassed !== this.state.errorPassed) {
      isError(value, (!required && value.length === 0) ? true : errorReg.test(value), id);
    }
  }

  render() {
    const { id, mandatory, enableFindAddress, label, required,
      autocomplete, maxlength, value, values, changeForm,
      button, onAddressLookup, addressLookupCalled, addressLookupData,
      lookupSelect, setloading, errorMessage, errorReg, isError } = this.props;
    const { errorPassed } = this.state;
    let buttonClass = 'ln-c-button ln-c-button--primary';
    let isHidden = required ? 'ln-c-label__info' : 'ln-c-label__info ln-u-visually-hidden';
    let hasError = !errorPassed ? 'ln-c-form-group ln-u-push-bottom has-error' : 'ln-c-form-group ln-u-push-bottom';
    const displayLoad = setloading ? 'block' : 'none';

    function LookupDisplay({ data }) {
      const count = data.length;
      if (count > 1) {
        return <Select
          id={config.formFields.addresslist.id}
          label={config.formFields.addresslist.label}
          required={config.formFields.addresslist.required}
          options={data}
          value="0"
          type="lookup"
          onSelect={e => {
            selectAddress(e.target.value)
          }}
        />;
      }

      if (count === 1) {
        return <Address
          houseNameOrNumber={data[0].houseNameOrNumber}
          line1={data[0].line1}
          line2={data[0].line2}
          postalCode={data[0].postalCode}
          town={data[0].town}
        />;
      }

      if (count === 0) {
        return <p className="tu-u-color-red">{errors.messages.addressPostcode}</p>;
      }

      return false
    }

    function selectAddress(value = "") {
      const newArray = []
      const address = addressLookupData.find(x => x.houseNameOrNumber === value);
      if (address) {
        newArray.push(address);
        lookupSelect(newArray);
      }
    }
    return (
      <div className="findAddress">
        <label className="ln-c-label" htmlFor={id}>{label}
          <span className={isHidden} aria-hidden="true">*</span>
        </label>
        <div className="paymentFormPostcodeLookup ln-o-grid ln-o-grid--matrix ln-o-grid--gutter-sm">

          <div className="ln-o-grid__item ln-u-1/2">
            <div className={hasError}>
              <div className="ln-c-input-container">
                <input
                  type="text"
                  className="ln-c-text-input ln-u-push-bottom"
                  autoComplete={autocomplete.toString()}
                  maxLength={maxlength}
                  id={id}
                  name={id}
                  required={required}
                  value={value}
                  onChange={e => {
                    changeForm(id, e.target.value)
                    isError(e.target.value, (!required && e.target.value.length === 0) ? true : errorReg.test(e.target.value), id)
                  }}
                  data-testid="postCode"
                />
                {enableFindAddress && <p className="ln-c-field-info ln-c-field-info--error">
                  {errorMessage}
                </p>}
              </div>
            </div>
          </div>
          <div className="ln-o-grid__item ln-u-1/2 address-button-container">
            <div className="ln-c-form-group ln-u-push-bottom">
              <button
                className={buttonClass}
                disabled={!enableFindAddress}
                onClick={e => {
                  e.preventDefault()
                  onAddressLookup()
                }}
                data-testid="findAddress"
              >{button.label}</button>
            </div>
          </div>
        </div>
        <div className="addressResults ln-u-display-block">
          <div className="addressResultsSpinner" style={{ display: displayLoad }}></div>
          <div style={{ display: addressLookupCalled ? 'block' : 'none' }}>
            {enableFindAddress && <LookupDisplay data={addressLookupData} />}
          </div>
        </div>
      </div>
    );
  }
}

AddressLookup.propTypes = {
  id: PropTypes.string.isRequired,
  enableFindAddress: PropTypes.bool.isRequired,
  mandatory: PropTypes.string,
  label: PropTypes.string.isRequired,
  required: PropTypes.bool.isRequired,
  autocomplete: PropTypes.bool,
  maxlength: PropTypes.number,
  value: PropTypes.string,
  values: PropTypes.object.isRequired,
  changeForm: PropTypes.func.isRequired,
  button: PropTypes.object,
  lookupSelect: PropTypes.func.isRequired,
  setloading: PropTypes.bool.isRequired,
  errorMessage: PropTypes.string,
  errorReg: PropTypes.any.isRequired,
  isError: PropTypes.func.isRequired,
  onAddressLookup: PropTypes.func.isRequired,
  errorArray: PropTypes.array.isRequired,
  addressLookupData: PropTypes.array.isRequired,
  addressLookupCalled: PropTypes.bool.isRequired
};

export default AddressLookup;
