import { connect } from 'react-redux';
import ManualForm from '../presentational/ManualForm';

const mapStateToProps = (state, ownProps) => {
  return {
    showCountry: state.formData.showCountry,
    countries: state.formData.countries,
    ...ownProps
  }
}

const ManualFormContainer = connect(mapStateToProps)(ManualForm);

export default ManualFormContainer;
