import React from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import DisplayCards from './container/DisplayCards';
import DifferentCard from './container/DifferentCard';
import AlternativePayment from './container/AlternativePayment';
import TermsAndConditions from './container/TermsAndConditions';
import LoadingScreen from './container/LoadingScreen';
import paypalService from './actions/paypal';
import { showCountry, setCountries } from './actions/index';
import config from './config/config';

const mapStateToProps = (state) => {
  return {
    showCountry: state.formData.showCountry,
    countries: state.formData.countries
  };
}

class PaymentApp extends React.Component {

  componentDidMount() {
    const paymentDetailsNode = document.getElementById('paymentDetails');
    const showCountryStatus = paymentDetailsNode.dataset.showcountry;
    if (showCountryStatus) {
      const { dispatch } = this.props;
      switch (showCountryStatus) {
        case 'true':
          dispatch(showCountry(true));
          break;
        case 'false':
          dispatch(showCountry(false));
        default:
          break;
      }
    }
  }

  onManualFormLoad = () => {
    if (this.props.showCountry) {
      const { countries, dispatch } = this.props;
      if (countries && !countries.length) {
        axios.get(config.formUrls.country)
          .then((response) => {
            const { data } = response;
            if (data) {
              dispatch(setCountries(data.map((countryObj) => {
                return { value: countryObj['isocode'], title: countryObj['name'] };
              })));
            }
          })
          .catch((err) => {
            console.warn('Error fetching countries', err);
          });
      }
    }
  }

  render() {
    return (
      <div>
        <div className="checkout-container checkout-container--nomargintop checkout-container--nomargin checkout-container--nopad-bottom">
          <div className="secure-checkout">
            <i className="lock"></i>
            <span>Secure Checkout</span>
          </div>
          <DisplayCards onManualFormLoad={this.onManualFormLoad} />
          <DifferentCard onManualFormLoad={this.onManualFormLoad} />
          {paypalService.getPayPalStatus() === true &&
            <AlternativePayment />
          }
        </div>
        <div className="checkout-container">
          <TermsAndConditions />
        </div>
        <LoadingScreen />
      </div>
    );
  }
}

const PaymentAppContainer = connect(mapStateToProps)(PaymentApp);

export default PaymentAppContainer;
