import config from '../config/config';

const setFormUrl = (selectedMethod, errorLength) => {
  if (selectedMethod === 'savedCards') {
    if (errorLength === 0) {
      return config.formUrls.edit;
    } else {
      return config.formUrls.payment;
    }
  } else if (selectedMethod === 'differentCard') {
    return config.formUrls.add;
  } else if (selectedMethod === 'alternativeCard') {
    return config.formUrls.noncetoken;
  } else {
    return config.formUrls.add;
  }
}

export default setFormUrl;
