const defaultValues = {
  houseNameOrNumber: '',
  line1: '',
  line2: '',
  town: ''
}

const createLookupObject = (values = defaultValues) => {
  const items = {
    addressDeliveryhouseNameOrNumber: values.houseNameOrNumber,
    addressDeliveryLine1: values.line1,
    addressDeliveryLine2: values.line2,
    addressDeliveryTownCity: values.town
  }
  return items;
};

export default createLookupObject;
