import React from 'react';
import PropTypes from 'prop-types';
import config from '../config/config';
import Icon from './Icon';
import BillingAddress from '../container/BillingAddress';
import ManualForm from '../container/ManualForm';

const Card = ({ index, id, cardType, cardNumber, expiryMonth, expiryYear, titleCode, accountHolderName, billingAddress, selectedMethod, displayForm, onItemChange, multiFormChange, onDisplayClick, addressValues, onFormChange, onAddressLookup, addressLookupData, addressLookupCalled, onLookupSelect, lookupLoading, manualForm, onShowManualForm, isError, errorArray }) => {
  const selected = addressValues.paymentCardId === id || (selectedMethod.length === 0 && index === 0) ? "selected" : "";
  const savedCardSelected = addressValues.paymentCardId === id ? "" : id;
  const formStatus = (selectedMethod === "savedCards" && displayForm && addressValues.paymentCardId === id) ? "block" : "none";
  const buttonStatus = formStatus === "block" ? "edit-billing-address edit-billing-address--active" : "edit-billing-address";
  const changeDisplayForm = displayForm ? false : true;
  const manualStatus = manualForm ? "block" : "none";
  const changeForm = manualForm ? false : true;

  if(selectedMethod.length === 0 && index === 0){
    loadDefault();
  }

  function loadDefault() {
    onItemChange();
    onFormChange("paymentCardId", savedCardSelected);
  }

  return (
      <li className="customer-options--item" id={`saved${id}`}>
        <div className="ln-c-form-option ln-c-form-option--radio">
          <input
            id={id}
            type="radio"
            name="paymentCardId"
            value={id}
            checked={selected}
            className="ln-c-form-option__input"
            onClick={() => {
              onItemChange();
              onFormChange("paymentCardId", savedCardSelected)
            }}
          />
          <label className="ln-c-form-option__label" htmlFor={id} tabIndex="0">
            <div className={`customer-options--title card-${cardType}`}>{cardNumber}</div>
            <div className="customer-options--information">
              <span className="customer-options--detail">
                Exp {expiryMonth}/{expiryYear} <br />
                {titleCode} {accountHolderName} <br />
                {billingAddress.houseNameOrNumber} {billingAddress.line1}, {billingAddress.town}, {billingAddress.postalCode.toUpperCase()}
              </span>
            </div>
          </label>
        </div>
        <span>
          <a
            href={`#saved${id}`}
            className={buttonStatus}
            onClick={e => {
              if (selectedMethod !== "savedCards") {
                onItemChange()
              }
              if (addressValues.paymentCardId !== id) {
                onFormChange("paymentCardId", savedCardSelected)
                onDisplayClick(true)
              } else {
                onDisplayClick(changeDisplayForm)
              }
            }}
            >
            {config.editAddress}
            <Icon classes="ln-c-icon ln-u-push-left" icon="chevlon-down" stroke="transparent" fill="#3D4042" viewbox="0 0 185.344 185.344" />
          </a>
          <span className="savedCardForm" style={{display: formStatus }}>
            <div className="editBillingAddress isAddAddresss">
              <div className="clearfix">
                <BillingAddress
                  isError={(val, result, id) => isError(val, result, id)}
                  manualForm={manualForm}
                  errorArray={errorArray}
                  multiFormChange={multiFormChange}
                  addressValues={addressValues}
                  onFormChange={onFormChange}
                  onAddressLookup={onAddressLookup}
                  addressLookupData={addressLookupData}
                  addressLookupCalled={addressLookupCalled}
                  onLookupSelect={onLookupSelect}
                  lookupLoading={lookupLoading}
                />
              </div>

              <div className="clearfix">
                <div className="showHideDiv">
                  <a href="#" onClick={e => {
                    e.preventDefault()
                    onShowManualForm(changeForm)
                  }}>
                    {changeForm ? "Enter address manually" : "Search for your address with a postalCode"}
                  </a>
                </div>
                <div style={{display: manualStatus}}>
                  <ManualForm
                    isError={(val, result, id) => isError(val, result, id)}
                    errorArray={errorArray}
                    addressValues={addressValues}
                    onFormChange={onFormChange}
                  />
                </div>
              </div>
            </div>
          </span>
        </span>
      </li>
  );
};

Card.propTypes = {
  id: PropTypes.string.isRequired,
  selectedMethod: PropTypes.string,
  displayForm: PropTypes.bool,
  cardType: PropTypes.string.isRequired,
  cardNumber: PropTypes.string.isRequired,
  expiryYear: PropTypes.string.isRequired,
  expiryMonth: PropTypes.string.isRequired,
  accountHolderName: PropTypes.string.isRequired,
  billingAddress: PropTypes.shape({
    houseNameOrNumber: PropTypes.string.isRequired,
    line1: PropTypes.string.isRequired,
    town: PropTypes.string.isRequired,
    postalCode: PropTypes.string.isRequired
  }),
  onItemChange: PropTypes.func.isRequired,
  multiFormChange: PropTypes.func.isRequired,
  onDisplayClick: PropTypes.func,
  addressValues: PropTypes.object.isRequired,
  addressLookupData: PropTypes.array.isRequired,
  addressLookupCalled: PropTypes.bool.isRequired,
  onFormChange: PropTypes.func.isRequired,
  onAddressLookup: PropTypes.func.isRequired,
  onLookupSelect: PropTypes.func.isRequired,
  lookupLoading: PropTypes.bool.isRequired,
  manualForm: PropTypes.bool.isRequired,
  onShowManualForm: PropTypes.func.isRequired,
  isError: PropTypes.func.isRequired,
  errorArray: PropTypes.array.isRequired
};

export default Card;
